.container {
    width: 100%;
    height: 100%;
}

.table {
    border: 1px solid #000000;
    border-radius: 5px;
    border-spacing: 0;
    width: 100%;
    table-layout: fixed;
}

.header th {
    border-bottom: 1px solid #000000;
}

.header th:not(:last-child) {
    border-right: 1px solid #eeeeee;
}

.table > tbody > tr:not(:last-child) td {
    border-bottom: 1px solid #eeeeee;
}

.table td {
    text-align: center;
    word-wrap: break-word;
}

.row td:not(:last-child) {
    border-right: 1px solid #eeeeee;
}

.title {
    font-size: 1.1rem;
    font-weight: 600;
    text-align: center;
}
