.athletePage {
    height: 100%;
}

.search {
    display: flex;
    flex-direction: column;
    justify-content: stretch;
    align-items: center;
}

.search > * {
    margin-top: 2vh;
}

.searchResults {
    display: flex;
    flex-direction: column;
    width: 95vw;
}

.searchResultHeader {
    display: flex;
    justify-content: space-between;
    margin: 0 1vw;
}

a {
    all: unset;
}

.searchResult {
    display: flex;
    justify-content: space-between;
    margin: 0 1vw;
    padding: 0.3vh 0;
    border-bottom: 1px solid black;
    cursor: pointer;
}

.search-result:hover {
    background-color: #cccccc;
}

.search-result .club {
    font-size: 0.9rem;
    text-align: right;
}

.subDetails {
    display: flex;
    font-size: 0.8rem;
    color: #888888;
}

.statisticsContent {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.athleteDetails {
    margin: 1vh 5vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.athleteDetails .name {
    font-weight: 600;
    font-size: 1.2rem;
    height: 100%;
    width: 50%;
}

.athleteDetails .subDetails {
    justify-content: flex-end;
}

.athleteDetails .subDetails .sex {
    margin-left: 2px;
}

.athleteDetails .club {
    font-size: 0.85rem;
    justify-content: space-around;
}

.athleteMedals {
    display: flex;
    flex-direction: column;
    font-size: 0.8rem;
    margin: 0 5vw;
}

.championshipMedals {
    display: flex;
}

.championshipMedals .medal {
    margin-left: 2px;
}

.goldMedal {
    color: #ffd700;
}

.silverMedal {
    color: #808080;
}

.bronzeMedal {
    color: #cd7f32;
}

.seasonSelector {
    display: flex;
    width: 100%;
    justify-content: flex-start;
    font-size: 0.8rem;
    overflow-x: scroll;
    color: #808080;
}

.seasonYear {
    margin: 0 1px;
    cursor: pointer;
}

.seasonSelector .selected {
    color: #000000;
}
