.headToHead {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.athletes {
    display: flex;
    justify-content: space-around;
    width: 100%;
}

.athlete {
    display: flex;
    justify-content: stretch;
    align-items: center;
    text-align: center;
    width: 45vw;
}

.athleteInformation {
    width: 100%;
}

.subDetails {
    display: flex;
    font-size: 0.8rem;
    color: #888888;
}

.comparison {
    display: flex;
    justify-content: center;
    width: 100%;
}

.statistics {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    width: 100%;
}

.statisticsDesktop {
    width: 45%;
}

.extraComparisonColumn {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    width: 30%;
    height: 100%;
}

.results {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.name {
    width: 100%;
    font-size: 1.2rem;
}

.subDetails {
    flex-direction: column;
}

.searchInputs {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 7vh;
}

.searchInputs input {
    width: 90%;
}

.eventSelect {
    display: flex;
    font-weight: 600;
    align-self: center;
}
