.loadingIcon {
    font-family: "Graduate";
    font-size: 2.5rem;
    text-align: center;
    width: 50px;
    height: 60px;
    margin: auto;
}

.progressBar {
    width: 100%;
    height: 5px;
    background-color: #eeeeee;
    border-radius: 5px;
}

.movingBlock {
    border-radius: 5px;
    position: relative;
    background-color: #000000;
    height: 5px;
    width: 40%;
    z-index: 2;
    animation: move 1.5s linear infinite;
}

@keyframes move {
    0% {
        left: 0;
    }
    50% {
        left: 60%;
    }
    100% {
        left: 0;
    }
}
