.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
}

.title {
    font-weight: bold;
    text-align: center;
}

.chart {
    width: 100%;
    aspect-ratio: 16/9;
    border: 1px solid black;
}

.control {
    display: flex;
    align-content: center;
    font-size: 0.9rem;
    margin: 1px 5px;
}

.controlGroupHeader {
    font-weight: bold;
}

.control input {
    margin: 0 5px;
}
