.modal {
    display: flex;
    flex-direction: column;
    position: absolute;
    border: 1px solid #808080;
    background-color: #ffffff;
    border-radius: 5px;
    top: 20%;
    left: 25%;
    width: 50%;
    aspect-ratio: 16/9;
    /* height: 50%; */
    z-index: 10;
    box-shadow: 0 0 5px #808080;
}

.top {
    display: flex;
    height: 30px;
    width: 100%;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid #000000;
    margin-bottom: 1px;
}

.title {
    margin-left: auto;
}

.body {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    justify-content: space-around;
    height: 100%;
    width: 100%;
    overflow-y: scroll;
}
