.selectAthleteZone {
    display: flex;
    width: 100%;
    border: 1px solid black;
}

.athleteCard {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 2px 5px;
    min-width: 100px;
    padding: 2px;
    height: 25px;
    border: 1px solid #808080;
    border-radius: 5px;
    cursor: pointer;
}

.addCard {
    min-width: 40px;
    color: 1px solid #808080;
}

.searchContainer {
    position: absolute;
    border: 1px solid #808080;
    background-color: #ffffff;
    border-radius: 5px;
    top: 20%;
    width: 50%;
    height: 50%;
    z-index: 10;
}

.athleteCard.selected {
    border: 1px solid #3461eb;
    box-shadow: 0 0 5px #3461eb;
}
