.dropdownSelector {
    position: relative;
    display: flex;
    user-select: none;
    cursor: pointer;
}
.selectedOption {
    display: flex;
    align-items: center;
}

.options {
    position: absolute;
    top: 100%;
    width: 100%;
    border: 1px solid #000000;
    z-index: 3;
    background-color: #ffffff;
}

.option:hover {
    background-color: #afc3ff;
}

.label {
    font-weight: 600;
    cursor: default;
    margin-right: 5px;
}

.arrow {
    font-size: 0.6rem;
}
