.toast {
    display: flex;
    flex-direction: column;
    position: fixed;
    left: 2.5vw;
    bottom: 50px;
    z-index: 5;
    border: 1px solid black;
    border-radius: 5px;
    background-color: white;
    width: 95vw;
    text-align: center;
}

.title {
    font-weight: bold;
}

.text {
    padding: 5px;
}

.buttonContainer {
    padding: 0 5px 5px 0;
    align-self: flex-end;
}
