.athleteEventCard {
    display: flex;
    width: 90%;
    height: 10vh;
    border: 1px solid #000000;
    margin: 1vh;
    align-items: center;
    flex-direction: column;
    border-radius: 3px;
    justify-content: space-between;
    transition: box-shadow 100ms linear 0ms;
}

.athleteEventCard.selected {
    border: 1px solid #3461eb;
    box-shadow: 0 0 5px #3461eb;
}

.mainRow {
    display: flex;
    width: 95%;
    justify-content: space-between;
    align-items: center;
    height: 100%;
}

.rowRight {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 45%;
    min-width: 120px;
}

.rankingDetails {
    display: flex;
    align-items: center;
    font-size: 0.9rem;
    width: 50%;
}

.currentRank {
    text-align: center;
}

.rankLabel {
    font-size: 0.75rem;
    color: #808080;
    line-height: 0.8;
    white-space: pre-wrap;
}

.bottomRow {
    position: relative;
    bottom: 0;
}

.name {
    font-weight: 600;
    font-size: 1.2rem;
}

.cardExpandCollapseButton {
    cursor: pointer;
    font-size: 0.7rem;
    color: #808080;
    padding: 0 2px;
    margin-bottom: 1px;
}

.eventCardExpanded {
    position: relative;
    top: -1vh;
    width: 88%;
    background-color: #eeeeee;
    z-index: -1;
}

.eventCardExpanded.hidden {
    height: 0;
    color: rgba(0, 0, 0, 0);
}

.expandedStatistics {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 0.9rem;
    margin-top: 5px;
}

.statisticRow {
    width: 80%;
    display: flex;
    justify-content: space-between;
}
