.container {
    width: 100%;
}

.row {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}

.row > div {
    margin: 0 5px;
}

.left {
    display: flex;
    width: 10%;
    text-align: center;
    align-items: center;
    font-size: 0.9rem;
}

.right {
    display: flex;
    flex-direction: column;
    width: 15%;
    align-items: center;
}

.performance {
    display: flex;
    width: 100%;
    justify-content: center;
}

.performance > div:first-child {
    margin-left: auto;
}

.date {
    font-size: 0.75rem;
    color: #808080;
}

.middle {
    flex-grow: 1;
    flex-direction: column;
}

.rank {
    text-align: center;
}

.name {
    font-weight: 500;
    font-size: 1.1rem;
}

.club {
    position: relative;
    font-size: 0.8rem;
    color: #808080;
    top: -2px;
}
